// CLIENTS PAGE - PREMIUM INTERACTIVITY

document.addEventListener('DOMContentLoaded', function() {
    // Initialize GSAP and plugins
    gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);
    
    // Set up custom cursor
    initCustomCursor();
    
    // Initialize page animations
    initPageAnimations();
    
    // Load and display client data
    loadClientData();
    
    // Initialize testimonial slider
    initTestimonialSlider();
    
    // Initialize case studies
    initCaseStudies();
    
    // Initialize magnetic buttons
    initMagneticButtons();
    
    // Initialize filter functionality
    initClientFilter();
});

document.addEventListener('DOMContentLoaded', () => {
    // Initialize animations
    initHeroAnimations();
    initScrollAnimations();
    initFilterButtons();
});

function initCustomCursor() {
    const cursor = document.querySelector('.custom-cursor');
    const follower = document.querySelector('.cursor-follower');
    
    if (window.innerWidth > 1024) {
        document.addEventListener('mousemove', (e) => {
            gsap.to(cursor, {
                x: e.clientX,
                y: e.clientY,
                duration: 0.1
            });
            
            gsap.to(follower, {
                x: e.clientX,
                y: e.clientY,
                duration: 0.6
            });
        });
        
        // Add hover effects for interactive elements
        const hoverElements = document.querySelectorAll('a, button, .client-card, .case-study-card, .filter-btn');
        
        hoverElements.forEach(el => {
            el.addEventListener('mouseenter', () => {
                gsap.to(cursor, {
                    scale: 0.5,
                    backgroundColor: 'white',
                    duration: 0.3
                });
                gsap.to(follower, {
                    scale: 1.2,
                    borderColor: 'white',
                    duration: 0.3
                });
            });
            
            el.addEventListener('mouseleave', () => {
                gsap.to(cursor, {
                    scale: 1,
                    backgroundColor: '#2a5bd7',
                    duration: 0.3
                });
                gsap.to(follower, {
                    scale: 1,
                    borderColor: '#2a5bd7',
                    duration: 0.3
                });
            });
        });
    } else {
        cursor.style.display = 'none';
        follower.style.display = 'none';
    }
}

function initPageAnimations() {
    // Hero section animations
    const heroTitle = document.querySelector('.hero-title');
    const heroSubtitle = document.querySelector('.hero-subtitle');
    const scrollHint = document.querySelector('.scroll-hint');
    
    // Split text for advanced animation
    if (heroTitle) {
        const text = heroTitle.textContent;
        heroTitle.innerHTML = '';
        
        for (let i = 0; i < text.length; i++) {
            const char = document.createElement('span');
            char.className = 'char';
            char.textContent = text[i] === ' ' ? '&nbsp;' : text[i];
            heroTitle.appendChild(char);
        }
        
        const chars = gsap.utils.toArray('.char');
        
        gsap.from(chars, {
            y: 50,
            opacity: 0,
            duration: 0.8,
            ease: 'power3.out',
            stagger: 0.03,
            delay: 0.3
        });
    }
    
    gsap.to(heroSubtitle, {
        y: 0,
        opacity: 1,
        duration: 1,
        ease: 'power3.out',
        delay: 0.8
    });
    
    gsap.to(scrollHint, {
        opacity: 1,
        duration: 1,
        ease: 'power3.out',
        delay: 1.5
    });
    
    // Section title animations
    gsap.utils.toArray('[data-animate="title"]').forEach(title => {
        gsap.from(title, {
            scrollTrigger: {
                trigger: title,
                start: 'top 80%',
                toggleActions: 'play none none none'
            },
            y: 50,
            opacity: 0,
            duration: 1,
            ease: 'power3.out'
        });
    });
    
    // Section subtitle animations
    gsap.utils.toArray('[data-animate="subtitle"]').forEach(subtitle => {
        gsap.from(subtitle, {
            scrollTrigger: {
                trigger: subtitle,
                start: 'top 80%',
                toggleActions: 'play none none none'
            },
            y: 30,
            opacity: 0,
            duration: 1,
            delay: 0.2,
            ease: 'power3.out'
        });
    });
    
    // Client card animations
    gsap.utils.toArray('.client-card').forEach((card, index) => {
        gsap.from(card, {
            scrollTrigger: {
                trigger: card,
                start: 'top 85%',
                toggleActions: 'play none none none'
            },
            y: 50,
            opacity: 0,
            scale: 0.95,
            duration: 0.8,
            delay: index * 0.1,
            ease: 'power3.out'
        });
    });
    
    // Case study animations
    gsap.utils.toArray('.case-study-card').forEach((card, index) => {
        gsap.from(card, {
            scrollTrigger: {
                trigger: card,
                start: 'top 85%',
                toggleActions: 'play none none none'
            },
            y: 50,
            opacity: 0,
            scale: 0.95,
            duration: 0.8,
            delay: index * 0.1,
            ease: 'power3.out'
        });
    });
}

